<template>
<div>
<div class="login-logo" >
 <router-link to="/" class="page-back">
 <mt-cell title="" icon="back" class="icon-style"></mt-cell>
 </router-link>
 <span class="span-text">密码登录</span>
</div>
<mt-field label="账号" placeholder="" v-model="username"></mt-field>
<mt-field label="密码" placeholder="" type="password" v-modal="password"><mt-switch v-model="value"> </mt-switch></mt-field>
<mt-field label="验证码" v-model="captcha">
 <img src="data:image/png;base64," height="45px" width="100px">
</mt-field>
<mt-button type="primary" size="large" style="background:#4cD964">登录</mt-button>
</div>
</template>
<script>
import Vue from 'vue'
import { Field } from 'mint-ui';
Vue.component(Field.name, Field);

import { Cell } from 'mint-ui';
Vue.component(Cell.name, Cell);
import { Switch } from 'mint-ui';
Vue.component(Switch.name, Switch);
export default {
	data(){
		return{

		}
	},
	mounted:{

	}
}

</script>
<style scope>
.login-logo{
	background:#3190E8 ;
    position: fixed;;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 1.95rem;

}
a{
	text-decoration: none;

}
.page-back{
	display: inline-block;
    top: 0px;
    left: 10px;
    position: absolute;
    width: 40px;
    height: 40px;
    text-align: center;
   
}
.icon-style{
     background: #3190E8;
     color:white;
     top: -2px;
}
.icon-style  .mint-cell-wrapper{
    border-bottom:none;
    background-image:  none;
}
.span-text{
    font-size:0.8rem;
    color:#fff;
    text-align:center;
    font-weight:bold;
    position: absolute;
    top: 9px;
    left: 38%;

}
.mint-cell-wrapper{
    border-bottom:1px solid #eaeaea;
    background-image:  none;
}
</style>


