<template>
<div class="main-index">
    <div class="xm-index">
  <span class="head-logo">ele.me</span>
  <router-link to="/login">
  <span class="login-span">
     登录|注册
  </span>
  </router-link>
    </div>
    <div class="swipe-slide">
      <mt-swipe  :auto="0">
        <mt-swipe-item>
          <router-link  class="a-block" :to="food1.nav"  v-for="food1 in foods1">
             <img :src="food1.imgurl">
             <span>{{food1.classify}}</span>
          </router-link>
         </mt-swipe-item>
    

        <mt-swipe-item>
          <router-link  class="a-block" :to="food2.nav"  v-for="food2 in foods2">
              <img :src="food2.imgurl">
              <span>{{food2.classify}}</span>
          </router-link>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    
    <div class="list-store">
      <div class="store-header">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-shangjia"></use>
          </svg>
         <span class="store-span">附近商家</span> 
      </div>

      
      <div class="store-shop">
        <ul>
          <router-link to='/detail'>
          <li v-for="shop in shops">
            <div class="section-img">
              <img src="https://fuss10.elemecdn.com/2/35/696aa5cf9820adada9b11a3d14bf5jpeg.jpeg">
            </div>
            <div class="section-text">
                <p> <span class="pinpai">{{shop.brand}}</span><span class="yanshi">{{shop.name}}</span><span class="text-style bao">{{shop.ticket}}</span></p>
                <p>
                   <svg class="icon svg-icon" aria-hidden="true">
                     <use xlink:href="#icon-wuxinghaopingx"></use>
                   </svg>
                   <svg class="icon svg-icon" aria-hidden="true">
                     <use xlink:href="#icon-wuxinghaopingx"></use>
                   </svg>
                   <svg class="icon svg-icon" aria-hidden="true">
                     <use xlink:href="#icon-wuxinghaopingx"></use>
                   </svg>
                   <svg class="icon svg-icon" aria-hidden="true">
                     <use xlink:href="#icon-wuxinghaopingx"></use>
                   </svg>
                   <svg class="icon svg-icon" aria-hidden="true">
                     <use xlink:href="#icon-star-"></use>
                   </svg>
                   <span class="text-style text-color">{{shop.grade}}</span><span class="text-style">{{shop.monthsell}}</span>
                   <span class="text-style fengniao">{{shop.amazon}}</span>
                   <span class="text-style  intime">{{shop.intime}}</span>
                </p>
                <p><span class="text-style">{{shop.Uptosend}}</span><span class="text-style">/{{shop.delivery}}</span><span class="text-style kilo">{{shop.distance}}</span><span class="text-style">/ {{shop.time}}</span></p>
            </div>

          </li>
        </router-link>
  
           
        </ul>
      </div>

    </div>
    
  <div class="footer">
    <router-link to="/index" class="child-link">
     <svg class="icon" aria-hidden="true">
       <use xlink:href="#icon-eleme"></use>
     </svg><p class="footer-text">外卖</p>
    </router-link>
    <router-link to="/search" class="child-link">
    <svg class="icon" aria-hidden="true">
      <use xlink:href="#icon-fujin"></use>
    </svg><p class="footer-text">搜索</p>
    </router-link>
    <router-link to="/order " class="child-link">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-dingdan" ></use>
      </svg><p class="footer-text">订单</p>
    </router-link>
    <router-link to="/my" class="child-link">
      <svg class="icon touxiang" aria-hidden="true">
        <use xlink:href="#icon-wode"></use>
      </svg><p class="footer-text">我的</p>
    </router-link>
  </div>
  
 <router-view></router-view>
</div>


</template>
<script type="text/javascript">
import {api} from "../global/api.js"
import Vue from 'vue'
import { Swipe, SwipeItem } from 'mint-ui';
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
import '../assets/css/iconfont.js'

 
 export default{
  data() {
  	return {
      foods1:[],
      foods2:[],
      shops:[]

  	}
  },
  methods:{
    toast:function(){
    	 // this.$toast("hehehe")//在外部main.js引用全部组件的写法，toast小写
    	 Toast('Upload Complete');//在本组件内import mint-ui 的写法。记得先import所需要的mint-ui，记得Toast大写
    },
    indicator:function(){
    this.$indicator.open('Loading...');
    },
    loadTop:function(){
    // load more data
    this.$refs.loadmore.onTopLoaded();
}

  },
    

     // '../../../static/index.json'
  mounted:function(){ 
    this.$http.get(api.eledata,{params:{"key":this.inputValue}}).then(
      function(response){
        console.log(response)
        console.log(response.data)
        this.foods1=response.data.lubo1;
        this.foods2=response.data.lubo2;
        this.shops=response.data.shops;


      }),
    function(response){
                alert("请求失败了")
              }
  }

  
}
</script>


<style scope>
a.router-link-active{
      color: #3190e8;
      
       
    }
.icon {
   width: 1em; 
   height: 1em;
   vertical-align: -0.15em;
   fill: #919191;
   overflow: hidden;
}
a{
  text-decoration: none;
}
 ul{
    list-style: none;
    margin:0px;
    padding:0px;
  }
 


 
p{
  padding: 0px;
  margin: 0px;
}
 
.touxiang{
  fill: #333;
}

a{
  text-decoration: none;
  color: black;
}
.main-index{
