import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import index from '@/components/index'
import login from '@/components/login'
import search from '@/components/views/search'
import order from '@/components/views/order'
import my from '@/components/views/my'
import detail from '@/components/views/detail'
import detail1 from '@/components/views/detail1'
import detail2 from '@/components/views/detail2'
import detail3 from '@/components/views/detail3'






Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      redirect:"/index"
      
    },
    {
      path: '/index',
      name: 'index',
      component: index,
       },
      
     {
      path:'/search',
      component:search
    },
     {
      path:'/order',
      component:order
    },
     {
      path:'/my',
      component:my
    },
     {
      path: '/login', 
      name: 'login',
      component: login
    },
    {
      path:'/store-detail',
      component:detail1
    },
     {
      path: '/detail', 
      name: 'detail',
      component: detail,
      children:[
        {path:'',component:detail1},
        {path:'detail1',component:detail1},
        {path:'detail2',component:detail2},
        {path:'detail3',component:detail3},





        
      

      ]
    }
    
  ]
})


 


